<template>
    <el-dialog class="BatchFilesUpload" :title="title" width="640px" center :visible.sync="dialogUploadVisible">
        <el-row>
            <el-upload
                class="upload-demo"
                drag
                :action="materialUpload"
                :on-change="handleChange"
                :on-remove="handleRemove"
                :on-success="handleAvatarSuccess"
                :on-error="handleAvatarError"
                :on-progress="handleProgress"
                :headers="apiHeader"
                :before-upload="beforeAvatarUpload"
                :file-list="fileList"
                :data="uploadData"
                :name="name"
                v-loading="loading"
                element-loading-text="..."
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(255, 255, 255, 0.8)"
            >
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">
                    <em></em>
                </div>
                <div class="el-upload__tip" slot="tip" v-if="fileTypes.length!=0">{{fileTypes.join(' / ')}}</div>
            </el-upload>
        </el-row>
        <el-row>
            <a :href="downloadUrl" download='download' class="mould-download-btn" v-if="urlTitle && downloadUrl">{{urlTitle}}</a>
        </el-row>
        <el-row class="upload-tips">
            <span>Excel</span>
        </el-row>
    </el-dialog>
</template>
<style lang="less">
.BatchFilesUpload {
    .el-dialog__header {
        font-weight: 600;
    }
    .el-dialog__body {
        padding: 25px 140px 10px;
        .mould-download-btn {
            color: #409eff;
            &:hover {
                color: #0c79e9;
            }
        }
        .upload-tips {
            font-size: 12px;
            margin-top: 10px;
        }
    }
}
</style>

<script>
import { getBaseUrl } from "../../util/config.js";
import { SAAS_TOKEN } from "../../util/keys.js";
import { getCookie } from "../../util/cookie.js";
export default {
    components: {},
    props: {
        title: {
            // 
            default: ""
        },
        urlTitle: {
            // 
            default: ""
        },
        downloadUrl: {
            // 
            default: ""
        },
        bVisible: {
            default: false
        },
        uploadUrl: {
            // 
            required: true,
            default: ""
        },
        fileTypes: {
            // 
            // required:true,
            default: () => []
        },
        uploadData: {
            // 
            default: undefined
        },
        name: {
            // 
            default: "name"
        }
    },
    data() {
        return {
            apiHeader: {},
            centerDialogVisible: true,
            fileList: [],
            loading: false
        };
    },
    computed: {
        materialUpload() {
            return `${getBaseUrl()}apigateway.gaodun.com/${this.uploadUrl}`;
        },
        dialogUploadVisible: {
            get: function() {
                return this.bVisible;
            },
            set: function() {
                this.$emit("handleCloesDialog");
            }
        }
    },
    methods: {
        beforeAvatarUpload(file) {
            if (this.fileTypes.length === 0) {
                return true;
            }
            var testmsg = file.name.substring(file.name.lastIndexOf(".") + 1);
            const extension =
                this.fileTypes.indexOf(testmsg) > -1 ? true : false;
            // const isLt2M = file.size / 1024 / 1024 < 10;
            if (!extension) {
                this.$message({
                    message: `${this.fileTypes.join("")}!`,
                    type: "warning"
                });
            }
            // if (!isLt2M) {
            //     this.$message({
            //         message: " 10MB!",
            //         type: "warning"
            //     });
            // }
            return extension;
        },
        handleChange(file, fileList) {
            this.fileList = fileList.slice(-1);
        },
        handleRemove(file, fileList) {
            this.fileList = fileList;
            // vue.$emit("uploadFile", {}, this.fileList);
        },
        handleProgress() {
            this.loading = true;
        },
        handleAvatarSuccess(res, file) {
            // console.log(res);
            this.loading = false;
            if (res.status == 0) {
                this.$message({
                    type: "success",
                    message: ""
                });
                this.$emit("uploadSuccessCallback");
            } else {
                this.$message({
                    type: "error",
                    message: `${res.message}`,
                    showClose: true,
                    duration: 5000
                });
                setTimeout(() => {
                    this.fileList = [];
                }, 0);
            }
        },
        handleAvatarError(err, file, fileList) {
            this.$message({
                type: "error",
                message: `${err}`,
                showClose: true,
                duration: 5000
            });
            this.loading = false;
            setTimeout(() => {
                this.fileList = [];
            }, 0);
        }
    },
    mounted() {},
    destroyed() {},
    created() {
        let token = "Basic " + getCookie(SAAS_TOKEN);
        this.apiHeader = { Authentication: token };
    }
};
</script>



